import { Value } from '@ali/4ever-cangjie';
import { isTable } from '../types';

export default function isSelectionAcrossTable(value: Value) {
  const { document, selection } = value;
  if (!selection?.isExpanded) {
    return false;
  }
  const { anchor, focus } = selection;
  const anchorTable = document.getClosest(anchor.key, isTable);
  const focusTable = document.getClosest(focus.key, isTable);
  if (!anchorTable && !focusTable) {
    return false;
  }
  return anchorTable !== focusTable;
}
